import { useXForm, XFormRender } from '@speedlo/xform'
import { endOfDay, startOfDay } from 'date-fns'
import React from 'react'

import { useBranchFilter } from '../../../components/filters/BranchFilter'
import { InputStatisticCreate } from '../../../graph/generated'

const context = React.createContext<InputStatisticCreate | undefined>(undefined)

export const useStatsFilterCurrent = () => React.useContext(context)

type TFilter = 'driver' | 'cashbox' | 'cashier' | 'sector' | 'origin'

export function useStatsFilter(...enabledFilters: RoA<TFilter>) {
  const [filter, setFilter] = React.useState<InputStatisticCreate | undefined>(
    undefined,
  )

  const initialValues = React.useMemo(
    () => ({
      dateTimeFrom: startOfDay(new Date()),
      dateTimeTo: endOfDay(new Date()),
      cashboxes: [] as ID[],
      cashiers: [] as ID[],
      drivers: [] as ID[],
      sectors: [] as ID[],
      origins: [] as ID[],
    }),
    [],
  )

  const enabledFiltersRef = React.useRef(enabledFilters)

  const applyFilter = React.useCallback(
    <T extends unknown[]>(name: TFilter, values: T) => {
      return enabledFiltersRef.current.includes(name) ? values : null
    },
    [],
  )

  const branch = useBranchFilter()

  const onSubmit = React.useCallback(
    (values: typeof initialValues) => {
      setFilter({
        branches: [branch],
        dateTimeFrom: values.dateTimeFrom,
        dateTimeTo: values.dateTimeTo,
        drivers: applyFilter('driver', values.drivers),
        cashBoxes: applyFilter('cashbox', values.cashboxes),
        cashiers: applyFilter('cashier', values.cashiers),
        sectors: applyFilter('sector', values.sectors),
        orderOrigins: applyFilter('origin', values.origins),
        sectorsWithChildren: true,
      })
    },
    [applyFilter, branch, initialValues],
  )

  const xform = useXForm({
    onSubmit,
    initialValues,
  })

  const renderForm = React.useCallback(
    (children: ReactNodes) => {
      return (
        <XFormRender xform={xform}>
          <context.Provider value={filter}>{children}</context.Provider>
        </XFormRender>
      )
    },
    [filter, xform],
  )

  return {
    filter,
    xform,
    executeFilter: xform.api.submitForm,
    renderForm,
  }
}
