import { useNow, useRouteParams, useRouterNavigate } from '@speedlo/hooks'
import { endOfDay, startOfDay } from 'date-fns/esm'
import React from 'react'

import { useBranchFilterEditable } from '../../../components/filters/BranchFilter'
import { useOrderListModel } from '../../order/hooks/useOrderListModel'
import { useStatsFilter } from '../hooks/useStatsFilter'
import { ROUTE_OVERVIEW_STATS_ORDERS } from '../overview.routes'

export const OrdersStatisticsLoader: React.FC<Required<SomeChildren>> = ({
  children,
}) => {
  const navigate = useRouterNavigate({})
  const params = useRouteParams<{ autoFilter: 'today' }>(
    `${ROUTE_OVERVIEW_STATS_ORDERS}/:autoFilter`,
  )

  const { xform, executeFilter, renderForm } = useStatsFilter('driver')

  const getNow = useNow()
  const orderListModel = useOrderListModel()
  const [, setBranch] = useBranchFilterEditable()

  React.useEffect(() => {
    if (params && params.autoFilter === 'today') {
      navigate({ to: ROUTE_OVERVIEW_STATS_ORDERS, replace: true })
      xform.api.mergeValues({
        dateTimeFrom: startOfDay(getNow()),
        dateTimeTo: endOfDay(getNow()),
      })
      setBranch(orderListModel.firstBranchId)
      executeFilter()
    }
  }, [
    executeFilter,
    getNow,
    navigate,
    orderListModel,
    params,
    setBranch,
    xform.api,
  ])

  return renderForm(children)
}
