import React from 'react'

import { useStatsFilter } from '../hooks/useStatsFilter'

export const SalesStatisticsLoader: React.FC<Required<SomeChildren>> = ({
  children,
}) => {
  const { renderForm } = useStatsFilter(
    'cashbox',
    'cashier',
    'sector',
    'origin',
  )
  return renderForm(children)
}
